<template>
  <div class="parkmap container">
    <div id="map" v-show="loadParkingData.length !== 0"></div>
    <div v-show="loadParkingData.length === 0"></div>
  </div>
</template>

<script>
// L  Leaflet
import L from "leaflet";

let openStreetMap = {};
export default {
  data() {
    return {
      parkingdata: this.loadParkingData,
    };
  },
  mounted() {
    openStreetMap = L.map("map", {
      center: [22.657409999999999, 120.29949999999999],
      //  zoom 
      //  18 
      zoom: 18,
    });

    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
      attribution:
        'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
      maxZoom: 20,
    }).addTo(openStreetMap);
    this.$store.getters.loadParkingData.forEach((pharmacy) => {
      let x = this.loadParkingData[0].latitude;
      let y = this.loadParkingData[0].longitude;
      openStreetMap.setView(new L.LatLng(x, y), 16);
      L.marker([pharmacy.latitude, pharmacy.longitude]).addTo(openStreetMap)
        .bindPopup(`<p style="margin:10px 0px;"><strong style="font-size: 20px;">${pharmacy.name}</strong></p>
          : ${pharmacy.type}<br>
          : ${pharmacy.area}<br>
          : ${pharmacy.address}<br>
          : ${pharmacy.phone}<br>
          <small>: ${pharmacy.price}</small>`);
    });
    console.log("mounted");
  },
  computed: {
    loadParkingData() {
      return this.$store.getters.loadParkingData;
    },
    setTime() {
      return this.showMark;
    },
  },
  watch: {
    loadParkingData() {
      this.showMark();
    },
  },
  methods: {
    showMark() {
      // clear markers
      let x = this.loadParkingData[0].latitude;
      console.log("", x);
      let y = this.loadParkingData[0].longitude;
      console.log("Y", y);
      openStreetMap.setView(new L.LatLng(x, y), 14);
      openStreetMap.eachLayer((layer) => {
        if (layer instanceof L.Marker) {
          openStreetMap.removeLayer(layer);
        }
      });

      this.loadParkingData.forEach((pharmacy) => {
        // 
        console.log("");
        L.marker([pharmacy.latitude, pharmacy.longitude]).addTo(openStreetMap);
      });
    },
  },
};
</script>

<style>
.parkmap {
  padding: 0 20px 60px;
}
#map {
  height: 60vh;
}
</style>
